define('lib/PrettyXML',[],
function ()
{
  'use strict';

  return {

    format: function (xml) {
      var formatted = '';
      var reg = /(>)\s*(<)(\/*)/g;
      xml = xml.toString().replace(reg, '$1\r\n$2$3');
      var pad = 0;
      var nodes = xml.split('\r\n');
      for(var n in nodes) {
	var node = nodes[n];
	var indent = 0;
	if (node.match(/.+<\/\w[^>]*>$/)) {
	  indent = 0;
	} else if (node.match(/^<\/\w/)) {
	  if (pad !== 0) {
            pad -= 1;
	  }
	} else if (node.match(/^<\w[^>]*[^\/]>.*$/)) {
	  indent = 1;
	} else {
	  indent = 0;
	}
	
	var padding = '';
	for (var i = 0; i < pad; i++) {
	  padding += '  ';
	}
  
	formatted += padding + node + '\r\n';
	pad += indent;
      }
      return formatted;
    }

  };
});

